body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App > .options-container {
  display: flex;
  /* flex-direction: column; */
  align-items: center;
  justify-content: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.filter-dropdown {
  color: #fff;
  padding: 8px 30px 9px 15px;
  border-radius: 4px;
  margin: 0 10px;
  font-size: 14px;
  -ms-box-shadow: inset 0 0 1px 1px #49ba8e;
  -o-box-shadow: inset 0 0 1px 1px #49ba8e;
  box-shadow: inset 0 0 1px 1px #49ba8e;
  min-width: 150px;
  z-index: 40;
  cursor: pointer;
}

.cards-container {
  display: flex;
  flex-wrap: wrap;
  /* justify-content: space-between; */
}

.event-card {
  margin: 20px 40px;
}

.event-name {
  font-size: 14px;
  color: #fff;
  text-align: left;
}

.date {
  position: absolute;
  min-width: 42px;
  border-radius: 50%;
  font-size: 11px;
  color: #fff;
  background-color: #4fd7a2;
  text-align: center;
  padding: 5px;
  display: flex;
  flex-direction: column;
  top: 10px;
  right: -220px;
}

.votes-count-and-text {
  margin-right: 5px;
  /* margin: 0;
  padding: 0;
  border: 0;
  font-weight: 400;
  vertical-align: baseline;
  background: 0 0; */
}

.votes-and-like-section {
  display: flex;
  flex-direction: column;
  color: #fff;
  font-size: 13px;
  position: absolute;
  bottom: 10px;
  /* left: 30px; */
  right: -100px;
}

.card-anchor {
  position: relative;
  display: inline;
  cursor: pointer;
}

.like-percentage {
  font-size: 20px;
}

.trailer-header {
  align-items: center;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  background-color: #000;
  height: 70px;
}

.left-btns {
  font-size: 12px;
  border-radius: 4px;
  padding: 10px 20px;
  margin-right: 15px;
  text-transform: uppercase;
  cursor: pointer;
}

.btn-inactive {
  background: #f3f3f3;
  color: #ccc;
}

.btn-active {
  color: #fff;
  background-color: #28a745;
  border-color: #28a745;
}

.left-header-section {
  margin-left: 10px;
}

.right-header-section {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  align-items: center;
}

.right-dropdwons {
  color: #4fd7a2;
  padding: 8px 30px 9px 15px;
  border-radius: 4px;
  margin: 0 10px;
  font-size: 14px;
  -ms-box-shadow: inset 0 0 1px 1px #49ba8e;
  -o-box-shadow: inset 0 0 1px 1px #49ba8e;
  box-shadow: inset 0 0 1px 1px #49ba8e;
  min-width: 140px;
  z-index: 40;
  cursor: pointer;
  background-color: #000;
  height: 40px;
}

.close-entity {
  cursor: pointer;
  margin: 10px;
  color: #A9A9A9;
}

.trailer-running-container {
  display: flex;
  justify-content: space-around;
}

.playing-trailer {
  width: 60%;
  height: 520px;;
}

.event-details {
  width: 30%;
  height: 520px;
}

.video-container {
  height: 100%;
  width: 100%;
}
